import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import { order } from './api.js'; // –Ü–º–ø–æ—Ä—Ç —Ñ—É–Ω–∫—Ü—ñ—ó order –¥–ª—è –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä
import localStorageAPI from './localStorage.js';

document.addEventListener('DOMContentLoaded', function () {
  const input = document.querySelector('.datetime'); // –í–∏–±—ñ—Ä –µ–ª–µ–º–µ–Ω—Ç—É –¥–ª—è —ñ–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—ó flatpickr
  const orderForm = document.querySelector('.cart-form'); // –í–∏–±—ñ—Ä —Ñ–æ—Ä–º–∏ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è

  const options = {
    enableTime: true,
    time_24hr: true,
    defaultDate: new Date(),
    minuteIncrement: 1,
  };

  flatpickr(input, options); // –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è flatpickr –¥–ª—è –≤–∏–±–æ—Ä—É –¥–∞—Ç–∏ —Ç–∞ —á–∞—Å—É

  orderForm.addEventListener('submit', async function (event) {
    event.preventDefault(); // –ó–∞–±–æ—Ä–æ–Ω–∞ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ—ó –ø–æ–≤–µ–¥—ñ–Ω–∫–∏ —Ñ–æ—Ä–º–∏

    const mail = document.getElementById('mail').value; // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–Ω–∞—á–µ–Ω–Ω—è –µ–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ—ó –ø–æ—à—Ç–∏ –∑ –ø–æ–ª—è –≤–≤–æ–¥—É

    const orderData = {
      mail,
    };

    console.log(orderData); // –í–∏–≤–µ–¥–µ–Ω–Ω—è –¥–∞–Ω–∏—Ö –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è –≤ –∫–æ–Ω—Å–æ–ª—å –¥–ª—è –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏

    try {
      // –í–∏–∫–ª–∏–∫ —Ñ—É–Ω–∫—Ü—ñ—ó order –¥–ª—è –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä
      const response = await order(orderData);
      console.log('Order sent successfully:', response);

      // –ó–±–µ—Ä–µ–∂–µ–Ω–Ω—è email –≤ localStorage –ø—ñ—Å–ª—è –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è
      localStorageAPI.save('lastOrder', orderData);
    } catch (error) {
      console.error('Error sending order:', error);
    }
  });
});
